<template>
	<div id="fisheries" v-title data-title="渔业管理">
		<arcgismap-list :showLandType="'yuye'" :data-target-flag="dataTargetFlag" :categoryList="[5]" :delete-aida-land="false" :deleteGreenLand="true"></arcgismap-list>
		<div class="layout-box-wrapper layout-left">
			<layout-box :disableBackground="true">
				<!--   水产养殖概况     -->
				<aquaculture></aquaculture>
				<!-- 生产作业上报 -->
				<div class="operators-dynamic-fisher">
					<box-item title="信息直报" show-sub-title sub-title="统计" @hand-sub-title="$router.push({name: 'farmRecordListOther'})">
						<operators-dynamic :categoryList="[5]" :farmAreaInfo="farmAreaInfo"></operators-dynamic>
					</box-item>
				</div>
			</layout-box>
		</div>
		<div class="layout-box-wrapper layout-right">
			<layout-box :disableBackground="true">
				<green-certification ref="child3"></green-certification>
				<aquatic-productsOutput ref="child4"></aquatic-productsOutput>
			</layout-box>
		</div>
	</div>
</template>
<script>
import {getStorage} from '@/utils/storage';
import {areaLevelCorAreaId} from '@/common/fn';
import arcgismapList from '@/components/map/arcgismap-list.vue';
import layoutBox from '@/components/common/LayoutBox';
import BoxItem from '@/components/common/BoxItem';
import operatorsDynamic from '@/components/common/operatorsDynamic';
export default {
	name: 'fisheries',
	data() {
		return {
			farmAreaInfo: {},
			dataTargetFlag: 0, // 数据源 0神农口袋 1种植系统
		};
	},
	created() {
		this.farmAreaInfo = areaLevelCorAreaId();
		var subjectVo = getStorage('govGovernmentSubjectVO');
		this.dataTargetFlag = Number(subjectVo.dataTargetFlag);
	},
	async mounted() {
		const That = this;

		function debounce(fn, delay) {
			let timer = null;
			return function () {
				const context = this;
				const args = arguments;
				clearTimeout(timer);
				timer = setTimeout(function () {
					fn.apply(context, args);
				}, delay);
			};
		}
		function resizeHander() {
			if (That.$route.path.indexOf('/home/fisheries') == -1) return;
			console.log('fisheries rezise');
			for (const key in That.$refs) {
				const childcomponent = That.$refs[key];
				childcomponent.refreshRender && childcomponent.refreshRender();
			}
		}
		window.resizefn = debounce(resizeHander, 200);
		window.removeEventListener('resize', window.resizefn, false);
		window.addEventListener('resize', window.resizefn, false);
	},
	methods: {},
	components: {
		BoxItem,
		operatorsDynamic,
		arcgismapList,
		layoutBox,
		businessScaleAnalysis: () => import('./components/business-scale-analysis.vue'), // 经营规模分析
		greenCertification: () => import('./components/green-certification.vue'), // 绿色认证
		aquaticProductsOutput: () => import('./components/aquatic-products-output.vue'), // 水产品产量
		aquaculture: () => import('./components/aquaculture'), // 水产养殖概况
	},
};
</script>
<style scoped lang="scss">
/* .map-view.show-aida-land #gismap_aidaland img {
	filter: hue-rotate(322deg) brightness(1.8);
	-webkit-filter: hue-rotate(322deg) brightness(1.8);
} */
#fisheries {
	position: relative;
	overflow: hidden;
	.layout-box-wrapper {
		position: absolute;
		top: 0;
		z-index: 22;
		width: 21.875vw;
		min-width: 400px;
		max-width: calc(420px * 2);
		height: 100%;
		min-height: 600px;
		&.layout-left {
			left: 0;
		}
		&.layout-right {
			right: 0;
		}
		/deep/ {
			.layout-box {
				.box-content {
					display: flex;
					flex-direction: column;
					justify-content: space-between;
				}
			}
		}
		.card-group {
			position: absolute;
			right: -250px;
		}
	}
}
.operators-dynamic-fisher {
	width: 100%;
	height: calc(55% - 15px);
	box-sizing: border-box;
	// background-color: rgba(4, 41, 64, 0.6);
}
</style>



